.Sidebar1 {
    width: 90px;
    height: 100vh;
    background-color:#f0f4f9;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    left: 0;
    top: 0;
  }
  .home_icon ,.account_icon
   ,.search_icon,.setting_icon,.menu_icon {
  cursor: pointer;
}
.home_icon, .search_icon, .account_icon {
    background-color: transparent;
    padding: 10px;
    border-radius: 50%;
    transition: background-color 0.3s ease;
  }
  
  .home_icon.active, .search_icon.active, .account_icon.active {
    background-color: #377bcfa3;
    color: white;
  }
  
  .Sidebar {
    position: relative; 
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 0;
    justify-content: space-between; 
  }
  .setting_icon.topLow {
    background-color: #e2e6eb;
    color: white;
  }
.menu_icon_hover, .setting_icon_hover {
    display: inline-block;
    border-radius: 8px;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.menu_icon_hover:hover, .setting_icon_hover:hover {
    background-color: #e2e6eb;
    transform: scale(1.1);
}

.menu_icon img, .setting_icon img,.middle_icon img {
    height: 25px;
}

.set {
    position: fixed;
    top: 20px;
}
.set2 {
    position: fixed;
    bottom: 30px;
}

.middle_icon{
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}
.home_icon,.account_icon,.search_icon{
    display: inline-block;
    padding: 10px;
    border-radius: 50%;
    transition: background-color 0.3s ease, transform 0.2s ease;
}
.setting_icon{
    display: inline-block;
    padding: 10px;
    transition: background-color 0.3s ease, transform 0.2s ease;
    border-radius: 1cap;
}
.home_icon:hover,.account_icon:hover,.search_icon:hover{
background-color: #377bcfa3;
    transform: scale(1.1);
}