.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.Card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100%; 
}

.CardFun {
  width: 58vmin;
  height: 60vmin;
  border-radius: 1cap;
  background-color: white;
  border: 1px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.Create_New_Event{
  font-size: 22px;
  font-weight: bold;
  margin-left: 15px;
  margin-top: 10px;
  color: #262626c9;
  display: flex;
  justify-content: space-between; 
  align-items: center; 
  width: 100%; 
}
.Create_New_Event img{
  display: flex;
  justify-content: flex-end; 
  height: 25px;
  cursor: pointer;
  margin-right: 20px;
  margin-top: 10px;
}
.hr{
  border: 0;              
  height: 2px;             
  background-color: #4847477f;  
  margin: 20px 0;
}
.Start_day,.End_time,.Event_title{
  font-weight: normal;
  margin-left: 15px;
  color: #262626c9;
  margin-top: 10px;
  margin-bottom: 10px;
}
.first_text,.sec_text{
  display: flex;
  justify-content: center;
  align-items: center;
}
.first_text input,.sec_text input{
  width: 54vmin;
  height: 5vmin;
  border-radius: 0.4cap;
  background-color: #ddd;
  border: none;
}
.textarea{
  width: 54vmin;
  height: 5vmin;
  border-radius: 0.4cap;
  resize: none;
}
.text_area{
  display: flex;
  justify-content: center;
  align-items: center;
}
.button{
  display: flex;
  justify-content: flex-end; 
  gap: 14px;
  margin-right: 15px;
  margin-top: 7vmin;
}
.delete-icon {
  height: 25px;
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.3s ease;
}
.delete-icon.show {
  opacity: 0; 
}
.close{
  height: 5vmin;
  width: 9vmin;
  border: none;
  background-color: #ddd;
  box-shadow: 0 4px 8px rgba(53, 27, 27, 0.1);
  border-radius: 0.4cap;
}
.Save{
  height: 5vmin;
  width: 18vmin;
  border: none;
  background-color: #1b9e04ca;
  box-shadow: 0 4px 8px rgba(53, 27, 27, 0.1);
  border-radius: 0.4cap;
}
