
All{
  flex: 1;
  min-height: 100vh;
  padding-bottom: 15vh;
  position: relative;
}


.finddate {
  height: 7vmin;
  width: 40vmin;
  border-radius: 2cap;
  background-color: #f0f4f9;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 4vh;
  margin-left: 16vh;
}

.butt {
  font-size: 18px;
  background-color: transparent;
  border: none;
  color: rgba(42, 41, 41, 0.589);
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.Buttom {
  display: flex;
  gap: 30px;
}

.butt:hover {
  color: black;
  cursor: pointer;
}

.findDay {
  margin-right: 8vh;
  margin-top: 4vh;
  position: absolute;
  right: 0;
  top: 0;
}

.findMounth {
  display: flex;
  text-align: center;
  justify-content: center;
  margin-top: -6vh;
}

.date {
  margin-left: 16vh;
  margin-top: 3vh;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
}

.calendar-box {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  width: 12vmax;
  height: 6.2vmax;
  text-align: center;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  transition: background-color 0.3s ease, transform 0.2s ease;
  cursor: pointer;
}

.calendar-box span {
  position: absolute;
  top: 3px;
  right: 9px;
  font-size: 14px;
  font-weight: bold;
  color: rgba(36, 36, 36, 0.845);
}

.calendar-box:hover {
  background-color: #f0f0f0;
}

.day-names {
  display: flex;
  grid-template-columns: repeat(7, 1fr);
  padding: 10px;
  gap: 11vmax;
  margin-left: 9vmin;
}

.day-name {
  text-align: center;
  font-weight: bold;
}
